<template>
  <div class="quiz-board-page">
    <AppHeader />
    <div class="content">
      <div class="quiz-board">
        <div class="search-area">
          <div class="total-count"> {{ filteredList.length }}</div>
          <div class="search-container">
            <select v-model="searchType" class="search-select">
              <option value="all"></option>
              <option value="title"></option>
              <option value="category"></option>
              <option value="level"></option>
              <option value="author"></option>
            </select>
            <input 
              type="text" 
              v-model="searchKeyword" 
              class="search-input" 
              placeholder=" "
              @keyup.enter="handleSearch"
            >
            <button class="search-button" @click="handleSearch"></button>
          </div>
        </div>
        <div class="table-container">
          <table class="quiz-table">
            <thead>
              <tr>
                <th width="10%"></th>
                <th width="50%"></th>
                <th width="20%"></th>
                <th width="20%"></th>
              </tr>
            </thead>
            <tbody>
              <template v-for="i in itemsPerPage" :key="i">
                <tr v-if="paginatedQuizList[i-1]">
                  <td class="id-cell">{{ paginatedQuizList[i-1].quizId }}</td>
                  <td class="title-cell" @click="goToQuizDetail(paginatedQuizList[i-1].quizId)">
                    <div class="title-wrapper">
                      <span class="title-text">{{ paginatedQuizList[i-1].quizTitle }}</span>
                      <span class="badges">
                        <span class="category-badge">{{ paginatedQuizList[i-1].quizCategory }}</span>
                        <span class="level-badge">{{ paginatedQuizList[i-1].quizLevel }}</span>
                      </span>
                    </div>
                  </td>
                  <td class="author-cell">{{ paginatedQuizList[i-1].memberNickname }}</td>
                  <td class="date-cell">{{ formatDate(paginatedQuizList[i-1].date) }}</td>
                </tr>
                <tr v-else class="empty-row">
                  <td colspan="4">&nbsp;</td>
                </tr>
              </template>
            </tbody>
          </table>
          <div class="pagination-container">
            <div class="pagination">
              <div class="pagination-wrapper">
                <div class="pagination-buttons">
                  <button class="nav-btn prev" @click="prevPage" :disabled="currentPage === 1">
                    &lt;
                  </button>
                  <div class="page-numbers">
                    <button 
                      v-for="pageNum in getDisplayedPages()" 
                      :key="pageNum"
                      :class="['page-num', { active: currentPage === pageNum }]"
                      @click="goToPage(pageNum)"
                    >
                      {{ pageNum }}
                    </button>
                  </div>
                  <button class="nav-btn next" @click="nextPage" :disabled="currentPage === totalPages">
                    &gt;
                  </button>
                </div>
              </div>
            </div>
            <button class="create-quiz-button search-button" @click="createQuiz"> </button>
          </div>
        </div>
      </div>
    </div>
    <AppFooter />
  </div>
</template>

<script>
import AppHeader from "./Header.vue";
import AppFooter from "./Footer.vue";
import axios from "axios";

export default {
  name: "HomePage",
  components: {
    AppHeader,
    AppFooter,
  },
  data() {
    return {
      quizList: [],
      currentPage: 1,
      itemsPerPage: 10,
      searchType: 'all',
      searchKeyword: '',
    };
  },
  computed: {
    filteredList() {
      if (!this.searchKeyword) return this.quizList;
      
      return this.quizList.filter(quiz => {
        const keyword = this.searchKeyword.toLowerCase();
        switch (this.searchType) {
          case 'title':
            return quiz.quizTitle.toLowerCase().includes(keyword);
          case 'category':
            return quiz.quizCategory.toLowerCase().includes(keyword);
          case 'level':
            return quiz.quizLevel.toLowerCase().includes(keyword);
          case 'author':
            return quiz.memberNickname.toLowerCase().includes(keyword);
          case 'all':
            return quiz.quizTitle.toLowerCase().includes(keyword) ||
                   quiz.quizCategory.toLowerCase().includes(keyword) ||
                   quiz.quizLevel.toLowerCase().includes(keyword) ||
                   quiz.memberNickname.toLowerCase().includes(keyword);
          default:
            return true;
        }
      });
    },
    paginatedQuizList() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.filteredList.slice(start, end);
    },
    totalPages() {
      return Math.ceil(this.filteredList.length / this.itemsPerPage);
    },
  },
  methods: {
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
    },
    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    async fetchQuizList() {
      const token = localStorage.getItem("jwtToken");
      if (!token) {
        console.error("Token is missing");
        return;
      }

      try {
        const beUrl = process.env.VUE_APP_BE_API_URL;
        const response = await axios.get(beUrl + "/api/v1/quizs/all", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        console.log("  :", response.data);

        //  DTO    
        this.quizList = response.data.map(quiz => {
          console.log(" :", quiz);
          return {
            quizId: quiz.quizId,
            memberNickname: quiz.memberNickname || '  ',
            quizCategory: quiz.quizCategory,
            quizTitle: quiz.quizTitle,
            quizLevel: quiz.quizLevel,
            quizAnswer: quiz.quizAnswer,
            quizDescription: quiz.quizDescription,
            date: Array.isArray(quiz.date) 
              ? new Date(quiz.date[0], quiz.date[1]-1, quiz.date[2], 
                        quiz.date[3], quiz.date[4], quiz.date[5])
              : new Date(quiz.date),
            count: quiz.count || 0
          };
        });

        this.quizList.sort((a, b) => b.quizId - a.quizId);
        
      } catch (error) {
        console.error("   :", error);
        alert("    .");
      }
    },
    goToFirstPage() {
      this.currentPage = 1;
    },
    goToLastPage() {
      this.currentPage = this.totalPages;
    },
    goToPage(pageNum) {
      this.currentPage = pageNum;
    },
    formatDate(date) {
      if (!date) return '';
      
      try {
        if (date instanceof Date) {
          return date.toLocaleDateString('ko-KR', {
            year: 'numeric',
            month: '2-digit',
            day: '2-digit'
          }).replace(/\. /g, '-').replace('.', '');
        }
        
        // date  
        if (Array.isArray(date)) {
          const newDate = new Date(date[0], date[1]-1, date[2]);
          return newDate.toLocaleDateString('ko-KR', {
            year: 'numeric',
            month: '2-digit',
            day: '2-digit'
          }).replace(/\. /g, '-').replace('.', '');
        }
        
        //  
        return new Date(date).toLocaleDateString('ko-KR', {
          year: 'numeric',
          month: '2-digit',
          day: '2-digit'
        }).replace(/\. /g, '-').replace('.', '');
      } catch (error) {
        console.error('  :', error, date);
        return '';
      }
    },
    getDisplayedPages() {
      const totalPages = this.totalPages;
      const current = this.currentPage;
      
      //  5  ,  
      if (totalPages <= 5) {
        return Array.from({ length: totalPages }, (_, i) => i + 1);
      }
      
      if (current <= 3) {
        return [1, 2, 3, 4, 5];
      }
      
      if (current >= totalPages - 2) {
        return [
          totalPages - 4,
          totalPages - 3,
          totalPages - 2,
          totalPages - 1,
          totalPages
        ];
      }
      
      return [current - 2, current - 1, current, current + 1, current + 2];
    },
    handleSearch() {
      this.currentPage = 1; //     
    },
    createQuiz() {
      this.$router.push('/quizform');  // router/index.js   
    },
    goToQuizDetail(quizId) {
      this.$router.push(`/quiz/${quizId}`);
    }
  },
  watch: {
    searchType() {
      this.currentPage = 1; //       
    }
  },
  async mounted() {
    const token = localStorage.getItem("jwtToken");

    if (!token) {
      console.error(" .");
      this.$router.push("/login");
      return;
    }

    await this.fetchQuizList();
  },
};
</script>

